* {
  box-sizing: border-box;
}

body, main, header, nav {
  margin: 0;
  padding: 0;
}

ul, h1, h2, h3, p {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  background: none;
  border: none;
}

:root {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__product-name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__product-name {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  font-size: 10px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  font-weight: 700;
  display: flex;
}

.card__price-text {
  color: #616070;
  line-height: 18px;
}

.card__price-cost {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition-duration: .3s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  place-content: space-between center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 16px;
  height: 16px;
}

.stars :not(.stars :first-child) {
  margin-left: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  justify-content: space-between;
  align-items: stretch;
  height: 60px;
  padding-right: 50px;
  display: flex;
}

.header__logo {
  align-self: center;
  width: 40px;
  height: 40px;
  margin-left: 50px;
  display: inline-block;
}

.cards-container {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards-container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .cards-container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .cards-container {
    --column-count: 4;
  }
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  font-weight: 500;
  text-decoration: none;
  transition-duration: .3s;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__list {
  height: 100%;
  margin-block: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  align-items: center;
  list-style: none;
  display: flex;
  position: relative;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
